import React from 'react';
import "./about.css";
import Rlogo from "../../logo.svg";

class About extends React.Component{
    render(){
        return(
            <div className="section" id="about">
                <div>
                    <h1 id="title-text">
                        This is About Section
                    </h1>
                    <p>
                        Mobile Banking from mobile payments, which involves the use of mobile devices.
                    </p>
                </div>
                <div id="about-cards">
                    <div className="cards">
                        <img src={Rlogo} alt="logo" />
                        <h3>Information</h3>
                        <p>Enter your Information to ensure a more safe and sound environment</p>
                    </div>
                    <div className="cards">
                        <img src={Rlogo} alt="logo" />
                        <h3>Data Secure</h3>
                        <p>Sending money has never been faster or easier</p>
                    </div>
                    <div className="cards">
                        <img src={Rlogo} alt="logo" />
                        <h3>Add Cards</h3>
                        <p>Add multiple cards and track your expenses on daily basis</p>
                    </div>
                </div>
                
            </div>
        )
    }
};

export default About;